import React from 'react';
import { connect } from "react-redux";
import { NavLink } from 'react-router-dom';
import Loader from '../../components/loader/Loader';
import { typeReducer, color } from './../../utilities/constants';
const mapStateToProps = (state) => {
    return {
        theme: state.theme
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        setTheme: (payload) => dispatch({
            type: typeReducer.theme, payload
        })
    }
}
const SignIn = (props) => {
    const { setTheme } = props;
    return (
        <div className='App'>
            Sign in
            <button className="btn" onClick={() => setTheme(color.themeDark)}>set theme dark</button>
            <button className="btn" onClick={() => setTheme(color.themeLight)}>set theme light</button>
            <Loader color={'#333'} size={10} />

            <NavLink to='/'>Back to Home</NavLink>
        </div>
    )
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SignIn);